.component--facet-card-listing {
  .component__title {
    margin: 0 0 20px;
  }

  .component__description {
    font-size: 20px;
    text-align: center;
    margin: 0 0 30px;
  }

  .component__filter_group {
    display: flex;
    justify-content: center;
  }

  .component__filter_list {
    display: inline-block;

    li {
      font-size: 16px;
      font-weight: 700;
      line-height: 18px;
      text-align: left;
      float: left;
      margin-left: 5px;
      margin-right: 5px;

      @include media($medium-min) {
        margin-left: 25px;
        margin-right: 25px;
      }

      &.component_filter_option {
        color: #2E3E53;
        text-transform: uppercase;
        border-bottom: 4px solid;
        border-color: transparent;
        padding-bottom: 4px;
        margin-bottom: 8px;
        cursor: pointer;

        @include media($medium-min) {
          padding-bottom: 8px;
        }
      }

      &.active,
      &:hover {
        border-color: inherit;
      }

      &.component__filter-by {
        color: #676357;
        width: 100%;

        @include media($medium-min) {
          width: auto;
        }
      }
    }
  }

  .component__content {
    display: flex;
    justify-content: center;
  }
}
