$partner-logo-gutter: 60px;

.node-type-homepage {

  #header {

    & > .main {
      max-width: 100%;
      padding: 0;
    }
  }

  .home {
    .field {
      margin-bottom: 0;
    }
    div.cover {
      top: -50%;
      left: -50%;
      width: 200%;
      height: 200%;
      img {
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        min-width: 50%;
        min-height: 50%;
        overflow-x: hidden;
      }
    }
    .center-align {
      text-align: center;
      h2 {
        margin-bottom: rem(5);
      }
      .subtitle {
        margin-bottom: rem(60);
      }
    }
    @include rem(font-size, 22px);
    p {
      @include rem(line-height, 32px);
      @include neuton;
      opacity: 0.87;
    }
    a {
      @include neuton;
      font-weight: 300;
    }
    img {
      height: 100%;
    }
    h1 {
      @include rem(font-size, 64px);
      @include rem(line-height, 72px);
      margin-bottom: rem(20);
      @include media($large) {
        @include rem(font-size, 34px);
        @include rem(line-height, 40px);
      }
    }
    h2 {
      @include rem(font-size, 36px);
      @include rem(line-height, 46px);
      @include media($large) {
        @include rem(font-size, 28px);
        @include rem(line-height, 36px);
      }
      margin-bottom: 15px;
    }
    .subtitle {
      color: #a9a49e;
      @include neuton;
      font-weight: 300;
      @include rem(font-size, 26px);
      line-height: 36px;
    }
    &__section {
      padding-top: rem(96);
      padding-bottom: rem(96);
    }

    &__get_involved {
      & + {
        .home__whats_new {
          margin-bottom: rem(96);
          padding: 0;
        }
      }
    }
