}

.country_selector.is-open {
  margin-left: 0;
  margin-right: 0;
  max-width: none;
}

.country_selector .main.collapsible--target {
  display: block;
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transition: max-height 0.3s, opacity 0.3s;
}

.country_selector .main.collapsible--target.is-open {
  max-height: 10000px;
  opacity: 1;
  overflow: visible;
  margin-bottom: 25px;
}

@media screen and (max-width: 640px) {
  .country_selector .main.collapsible--target {
    max-height: 10000px;
    opacity: 1;
    overflow: visible;
    margin-bottom: 25px;
  }
}

.country_selector .collapsible--trigger {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.country_selector .collapsible--trigger span {
  position: relative;
  bottom: -30px;
}

.country_selector .open_icon {
  display: block;
  margin: 0 auto;
  height: 62px;
  width: 125px;
  border-radius: 90px 90px 0 0;
  background-color: white;
  opacity: 0;
  transition: opacity 0.3s;
}

.country_selector .open_icon:after {
  border-bottom: 3px solid #cac9c4;
  border-left: 3px solid #cac9c4;
  content: "";
  display: inline-block;
  height: 0.75rem;
  margin: 1.4375rem 0 0 3.6rem;
  transform: rotate(135deg);
  transition: transform 0.3s ease;
  vertical-align: middle;
  width: 0.75rem;
}

.country_selector .open_icon.is-open {
  opacity: 1;
}

.country_selector .closed_text {
  font-family: 'Source Sans Pro', 'Arial', sans-serif;
  font-weight: 400;
  display: block;
  font-size: 0.875rem;
  font-weight: 700;
  color: #949189;
  height: 0;
  overflow: hidden;
  white-space: nowrap;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  transition: height 0.3s;
}

.country_selector .closed_text.is-open {
  height: 50px;
  line-height: 50px;
  bottom: 0;
}

