  display: none;
}

.indicator__actions-confidence-intervals input[type='checkbox']:checked ~ .custom-checkbox:before {
  border-color: #949189;
  -webkit-animation: check 0.3s;
  -moz-animation: check 0.3s;
  animation: check 0.3s;
}

.indicator__actions-confidence-intervals .custom-checkbox {
  border: 1px solid #d1d0cc;
  display: inline-block;
  height: 1rem;
  margin: -0.125rem 0.3125rem 0 0;
  position: relative;
  vertical-align: middle;
  width: 1rem;
}

.indicator__actions-confidence-intervals .custom-checkbox:before {
  border-bottom: 2px solid transparent;
  border-left: 2px solid transparent;
  content: '';
  display: block;
  height: 0.3125rem;
  left: 2.5px;
  position: absolute;
  top: 7.5px;
  -webkit-transform: scaleX(1) rotate(-50deg);
  -moz-transform: scaleX(1) rotate(-50deg);
  -ms-transform: scaleX(1) rotate(-50deg);
  -o-transform: scaleX(1) rotate(-50deg);
  transform: scaleX(1) rotate(-50deg);
  -webkit-transform-origin: left top;
  -moz-transform-origin: left top;
  -ms-transform-origin: left top;
  -o-transform-origin: left top;
  transform-origin: left top;
  width: 0.5625rem;
}

@-webkit-keyframes check {
  0% {
    width: 0;
    height: 0;
  }
  25% {
    width: 0;
    height: 2px;
  }
  50% {
    width: 2px;
    height: 5px;
  }
  75% {
    width: 7px;
  }
  100% {
    width: 9px;
  }
}

@-moz-keyframes check {
  0% {
    width: 0;
    height: 0;
  }
  25% {
    width: 0;
    height: 2px;
  }
  50% {
    width: 2px;
    height: 5px;
  }
  75% {
    width: 7px;
  }
  100% {
    width: 9px;
  }
}

@keyframes check {
  0% {
    width: 0;
    height: 0;
  }
  25% {
    width: 0;
    height: 2px;
  }
  50% {
    width: 2px;
