/* -----------------------------------------
   Shared Styles
----------------------------------------- */
@media only screen and (max-width: 1200px) {
    table.responsive {
        margin-bottom: 0;

        td, th {
            position: relative; white-space: nowrap; overflow: hidden;
            
            &:first-child {
                display: none;
            }
        } // td, th

        &.pinned td {
            display: none;
        }

    } // table.responsive

    .table-wrapper {
        position: relative;
        margin-bottom: 20px;
        overflow: hidden;
        //border-right: 1px solid #ccc;
        
        .scrollable { margin-left: 35%; overflow: scroll; overflow-y: hidden; }    

    } // .table-wrapper

    .pinned {
        position: absolute;
        left: 0;
        top: 0;
        background: #fff;
        width: 35%;
        overflow: hidden;
        overflow-x: auto;
        //border-right: 1px solid #ccc;
        //border-left: 1px solid #ccc;

        table {
            border-right: none; border-left: none; width: 100%;

            th, td {
                white-space: nowrap;
            }

            td {
            
                &:last-child {
                    border-bottom: 0;
                }
            } // td
        } // table
    } // .pinned
}